'use client';
import React, { useState } from 'react';
import Sidebar from './sidebar';
import GoogleMapsComponent from './map';
import Topbar from './topbar';

export default function Home() {
  const [isPinMode, setIsPinMode] = useState(false);
  const [radius, setRadius] = useState(5);
  // NEW: Track if a pin currently exists on the map
  const [hasActivePin, setHasActivePin] = useState(false);

  return (
    <div className="flex flex-col h-screen w-full">
      <Topbar />
      
      <div className="flex flex-1 overflow-hidden">
        <Sidebar 
          isPinMode={isPinMode} 
          onPinClick={() => setIsPinMode(!isPinMode)}
          radius={radius}
          onRadiusChange={setRadius}
          // NEW: Pass delete logic to sidebar
          hasActivePin={hasActivePin}
          onDeletePin={() => setHasActivePin(false)} 
        />

        <main className="flex-1 h-full relative">
          <GoogleMapsComponent 
            isPinMode={isPinMode} 
            setPinMode={setIsPinMode} 
            searchRadius={radius}
            // NEW: Pass state to map
            hasActivePin={hasActivePin}
            setHasActivePin={setHasActivePin}
          />
        </main>
      </div>
    </div>
  );
}