'use client';
import React from 'react';
import PinButton from './pinButton';

interface SidebarProps {
  isPinMode: boolean;
  onPinClick: () => void;
  radius: number;
  onRadiusChange: (val: number) => void;
}

export default function Sidebar({ isPinMode, onPinClick, radius, onRadiusChange }: SidebarProps) {
  return (
    <aside className="w-[350px] h-full bg-white border-r shadow-md flex flex-col p-6 space-y-6 z-10">
      <h2 className="text-xl font-bold">Property Search</h2>
      
      {/* Radius Input */}
      <div className="flex flex-col space-y-2">
        <label className="text-sm font-medium text-gray-600">Search Radius (miles)</label>
        <input 
          type="number" 
          value={radius}
          onChange={(e) => onRadiusChange(Number(e.target.value))}
          className="border rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-500 outline-none"
          placeholder="e.g. 5"
        />
      </div>
    {/* Inside Sidebar.tsx */}
    <div className="flex flex-col space-y-2">
  <div className="flex justify-between">
    <label className="text-sm font-medium text-gray-600">Search Radius</label>
    <span className="text-sm font-bold text-blue-600">{radius} miles</span>
  </div>
  <input 
    type="range" 
    min="1" 
    max="50" 
    value={radius}
    onChange={(e) => onRadiusChange(Number(e.target.value))}
    className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-blue-600"
  />
</div>
      {/* Pin Button */}
      <div className="pt-4 border-t">
        <p className="text-xs text-gray-500 mb-2">Click below then click on the map to mark a property.</p>
        <PinButton isPinning={isPinMode} onClick={onPinClick} />
      </div>

      {/* Placeholder for results */}
      <div className="flex-1 overflow-y-auto">
        <p className="text-sm italic text-gray-400">Results will appear here...</p>
      </div>
    </aside>
  );
}