'use client';
import React, { useEffect, useRef, useState } from 'react';

const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

interface MapProps {
  isPinMode: boolean;
  setPinMode: (value: boolean) => void;
  searchRadius: number;
}

export default function GoogleMapsComponent({ isPinMode, setPinMode, searchRadius }: MapProps) {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<google.maps.Map | null>(null);
  
  // These REFS keep track of the current pin and circle so we can update/remove them
  const currentMarkerRef = useRef<any>(null); 
  const currentCircleRef = useRef<google.maps.Circle | null>(null);
  
  const [isLoaded, setIsLoaded] = useState(false);

  // 1. LOAD GOOGLE MAPS SCRIPT
  useEffect(() => {
    if (window.google?.maps) {
      setIsLoaded(true);
      return;
    }
    const script = document.createElement('script');
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAPS_API_KEY}&v=weekly&libraries=marker`;
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.head.appendChild(script);
  }, []);

  // 2. INITIALIZE THE MAP INSTANCE (Once)
  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;

    const map = new google.maps.Map(mapRef.current, {
      zoom: 13,
      center: { lat: 40.7200, lng: -73.8440 },
      mapId: 'DEMO_MAP_ID', // Replace with your real Map ID later
    });

    mapInstanceRef.current = map;
  }, [isLoaded]);

  // 3. REACTIVE RADIUS: Update circle size immediately when slider moves
  useEffect(() => {
    if (currentCircleRef.current) {
      const meters = searchRadius * 1609.34;
      currentCircleRef.current.setRadius(meters);
    }
  }, [searchRadius]);

  // 4. PINNING LOGIC: Listen for click when isPinMode is active
  useEffect(() => {
    if (!mapInstanceRef.current || !isPinMode) return;
    const map = mapInstanceRef.current;

    // Visual feedback that user is in "Pin Mode"
    map.setOptions({ draggableCursor: 'crosshair' });

    const clickListener = map.addListener('click', async (e: google.maps.MapMouseEvent) => {
      if (!e.latLng) return;

      // CLEANUP: Remove old pin and circle before placing new ones
      if (currentMarkerRef.current) currentMarkerRef.current.map = null;
      if (currentCircleRef.current) currentCircleRef.current.setMap(null);

      const { AdvancedMarkerElement } = await google.maps.importLibrary("marker") as google.maps.MarkerLibrary;

      // CREATE THE PIN (Redfin Style)
      const pinElement = document.createElement('div');
      pinElement.className = 'bg-white text-blue-600 px-3 py-1 rounded-full border-2 border-blue-600 font-bold shadow-lg text-sm';
      pinElement.innerText = '$ New';

      const newMarker = new AdvancedMarkerElement({
        map,
        position: e.latLng,
        content: pinElement,
      });
      currentMarkerRef.current = newMarker;

      // CREATE THE CIRCLE
      const newCircle = new google.maps.Circle({
        map,
        center: e.latLng,
        radius: searchRadius * 1609.34,
        fillColor: "#1a73e8",
        fillOpacity: 0.2,
        strokeColor: "#1a73e8",
        strokeWeight: 1,
      });
      currentCircleRef.current = newCircle;

      // RESET UI
      setPinMode(false);
      map.setOptions({ draggableCursor: null });
    });

    return () => {
      google.maps.event.removeListener(clickListener);
      if (mapInstanceRef.current) mapInstanceRef.current.setOptions({ draggableCursor: null });
    };
  }, [isPinMode, searchRadius, setPinMode]);

  return (
    <div className="w-full h-full relative">
      {!isLoaded && (
        <div className="absolute inset-0 flex items-center justify-center bg-gray-50 z-20">
          Loading Map...
        </div>
      )}
      <div ref={mapRef} className="w-full h-full" />
    </div>
  );
}